import React, { useState } from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Button,
  ButtonGroup,
  Grid,
  makeStyles,
  LinearProgress,
} from '@material-ui/core';
import AssignmentIcon from '@material-ui/icons/Assignment';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import EmojiEventsIcon from '@material-ui/icons/EmojiEvents';
import ViewAgendaIcon from '@material-ui/icons/ViewAgenda';
import ViewStreamIcon from '@material-ui/icons/ViewStream';
import { questionnaireData } from '../../../data/questionnaireData';
import { computeScore, getRecommendedPlan, getMaxScore } from '../../../helpers/planScoring';
import QuestionCard from './QuestionCard';
import ResultCard from './ResultCard';
import AnswerSummary from './AnswerSummary';
import payrollImg from '../../../images/payroll.png';
import { useContentContainerStyles } from '../../../styles/contentContainer';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(4, 3),
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(3, 2),
    },
    [theme.breakpoints.down('xs')]: {
      padding: theme.spacing(2, 1),
    },
  },
  header: {
    textAlign: 'center',
    marginBottom: theme.spacing(4),
  },
  logoWrap: {
    width: 64,
    height: 64,
    borderRadius: '50%',
    backgroundColor: 'rgba(219, 103, 0, 0.12)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    margin: '0 auto',
    marginBottom: theme.spacing(2),
  },
  logoImg: {
    width: 100,
    height: 80,
    color: '#DB6700',
  },
  title: {
    fontWeight: 700,
    color: '#1a1a1a',
  },
  subtitle: {
    color: '#666',
    marginTop: theme.spacing(1),
  },
  layoutToggle: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  layoutBtn: {
    textTransform: 'none',
    fontSize: '0.875rem',
  },
  layoutBtnActive: {
    backgroundColor: 'rgba(219, 103, 0, 0.15)',
    color: '#DB6700',
    '&:hover': {
      backgroundColor: 'rgba(219, 103, 0, 0.25)',
    },
  },
  scrollWrap: {
    display: 'flex',
    flexDirection: 'column',
    gap: theme.spacing(2),
    marginBottom: theme.spacing(4),
  },
  progress: {
    marginBottom: theme.spacing(4),
    borderRadius: 4,
    height: 6,
  },
  progressStickyWrap: {
    position: 'sticky',
    top: 60,
    zIndex: 10,
    backgroundColor: '#fff',
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  actions: {
    display: 'flex',
    justifyContent: 'space-between',
    marginTop: theme.spacing(3),
    gap: theme.spacing(2),
  },
  navBtn: {
    textTransform: 'none',
  },
  resultWrap: {
    width: '100%',
    maxWidth: 960,
    margin: '0 auto',
  },
  resultGrid: {
    alignItems: 'stretch',
  },
  resultCardWrapper: {
    display: 'flex',
    height: '100%',
  },
  resultHeader: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: 12,
    marginBottom: theme.spacing(3),
  },
  resultHeaderIcon: {
    fontSize: 36,
    color: '#DB6700',
  },
  backBtn: {
    marginBottom: theme.spacing(2),
    textTransform: 'none',
  },
  resultActions: {
    display: 'flex',
    justifyContent: 'center',
    gap: theme.spacing(2),
    marginTop: theme.spacing(4),
    flexWrap: 'wrap',
  },
}));

const LAYOUT_STEP = 'step';
const LAYOUT_SCROLL = 'scroll';

const PlanRecommendation = () => {
  const classes = useStyles();
  const contentClasses = useContentContainerStyles();
  const navigate = useNavigate();
  const [layout, setLayout] = useState(LAYOUT_STEP);
  const [step, setStep] = useState(0);
  const [answers, setAnswers] = useState({});

  const totalSteps = questionnaireData.length;
  const isComplete = step >= totalSteps;
  const currentItem = questionnaireData[step];

  const handleAnswer = (id, val) => {
    setAnswers((prev) => ({ ...prev, [id]: val }));
  };

  const canProceed = () => {
    if (!currentItem) return false;
    const v = answers[currentItem.id];
    if (currentItem.type === 'multiple') return Array.isArray(v) && v.length > 0;
    return v != null && v !== '';
  };

  const allAnswered = () => {
    return questionnaireData.every((item) => {
      const v = answers[item.id];
      if (item.type === 'multiple') return Array.isArray(v) && v.length > 0;
      return v != null && v !== '';
    });
  };

  const answeredCount = questionnaireData.filter((item) => {
    const v = answers[item.id];
    if (item.type === 'multiple') return Array.isArray(v) && v.length > 0;
    return v != null && v !== '';
  }).length;

  const handleNext = () => {
    if (step < totalSteps - 1) setStep((s) => s + 1);
    else setStep(totalSteps);
  };

  const handleBack = () => {
    setStep((s) => Math.max(0, s - 1));
  };

  const handleCheckout = () => {
    navigate(`/contact-us?plan=${recommendedPlan?.id || ''}`);
  };

  const score = computeScore(answers);
  const recommendedPlan = getRecommendedPlan(score);

  return (
    <Container maxWidth="md" className={`${classes.root} ${contentClasses.root}`}>
      <Button
        onClick={() => navigate(-1)}
        startIcon={<ArrowBackIcon />}
        className={classes.backBtn}
        color="primary"
      >
        Back
      </Button>
      {!isComplete && (
        <Box className={classes.header}>
          <Box className={classes.logoWrap}>
            <img src={payrollImg} alt="UlapPayroll" className={classes.logoImg} />
          </Box>
          <Typography variant="h5" className={classes.title}>
            UlapPayroll Plan Recommendation
          </Typography>
          <Typography variant="body2" className={classes.subtitle}>
            Answer a few questions and we&apos;ll recommend the right plan for you
          </Typography>
          <Box display="flex" justifyContent="center" className={classes.layoutToggle}>
            <ButtonGroup size="small" aria-label="questionnaire layout">
              <Button
                className={`${classes.layoutBtn} ${layout === LAYOUT_STEP ? classes.layoutBtnActive : ''}`}
                onClick={() => setLayout(LAYOUT_STEP)}
                startIcon={<ViewAgendaIcon style={{ fontSize: 18 }} />}
              >
                Step by step
              </Button>
              <Button
                className={`${classes.layoutBtn} ${layout === LAYOUT_SCROLL ? classes.layoutBtnActive : ''}`}
                onClick={() => setLayout(LAYOUT_SCROLL)}
                startIcon={<ViewStreamIcon style={{ fontSize: 18 }} />}
              >
                Scroll all
              </Button>
            </ButtonGroup>
          </Box>
        </Box>
      )}

      {!isComplete && layout === LAYOUT_SCROLL ? (
        <Box className={classes.progressStickyWrap}>
          <LinearProgress
            variant="determinate"
            value={(answeredCount / totalSteps) * 100}
            className={classes.progress}
            color="primary"
          />
        </Box>
      ) : !isComplete && (
        <LinearProgress
          variant="determinate"
          value={((step + 1) / totalSteps) * 100}
          className={classes.progress}
          color="primary"
        />
      )}

      {!isComplete && layout === LAYOUT_SCROLL ? (
        <Box className={classes.scrollWrap}>
          {questionnaireData.map((item) => (
            <QuestionCard
              key={item.id}
              item={item}
              value={answers[item.id]}
              onChange={handleAnswer}
            />
          ))}
          <Button
            variant="contained"
            color="primary"
            disabled={!allAnswered()}
            onClick={() => setStep(totalSteps)}
            endIcon={<ArrowForwardIcon />}
            className={classes.navBtn}
            style={{ alignSelf: 'center', marginTop: 16 }}
          >
            See Result
          </Button>
        </Box>
      ) : !isComplete && currentItem ? (
        <>
          <QuestionCard
            item={currentItem}
            value={answers[currentItem.id]}
            onChange={handleAnswer}
          />
          <Box className={classes.actions}>
            <Button
              disabled={step === 0}
              onClick={handleBack}
              startIcon={<ArrowBackIcon />}
              className={classes.navBtn}
            >
              Back
            </Button>
            <Button
              variant="contained"
              color="primary"
              disabled={!canProceed()}
              onClick={handleNext}
              endIcon={<ArrowForwardIcon />}
              className={classes.navBtn}
            >
              {step === totalSteps - 1 ? 'See Result' : 'Next'}
            </Button>
          </Box>
        </>
      ) : (
        <Box className={classes.resultWrap}>
          <Box className={classes.resultHeader}>
            <EmojiEventsIcon className={classes.resultHeaderIcon} />
            <Typography variant="h5" style={{ fontWeight: 700, color: '#1a1a1a' }}>
              Recommended for you
            </Typography>
          </Box>
          <Grid container spacing={3} className={classes.resultGrid}>
            <Grid item xs={12} md={6} className={classes.resultCardWrapper}>
              <ResultCard
                plan={recommendedPlan}
                score={score}
                maxScore={getMaxScore()}
                onCheckout={handleCheckout}
              />
            </Grid>
            <Grid item xs={12} md={6} className={classes.resultCardWrapper}>
              <AnswerSummary answers={answers} questionnaireData={questionnaireData} />
            </Grid>
          </Grid>
          <Box className={classes.resultActions}>
            <Button color="primary" onClick={() => setStep(0)}>
              Review
            </Button>
          </Box>
        </Box>
      )}
    </Container>
  );
};

export default PlanRecommendation;
