import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  Checkbox,
  Box,
  makeStyles,
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  card: {
    borderRadius: 12,
    boxShadow: theme.shadows[2],
    marginBottom: theme.spacing(3),
    [theme.breakpoints.down('xs')]: {
      marginBottom: theme.spacing(2),
    },
  },
  sectionTitle: {
    color: '#DB6700',
    fontWeight: 600,
    marginBottom: theme.spacing(1),
    fontSize: '0.85rem',
  },
  question: {
    fontWeight: 600,
    marginBottom: theme.spacing(2),
  },
  option: {
    marginBottom: theme.spacing(0.5),
    borderRadius: 8,
    padding: theme.spacing(0.5, 1),
    '&:hover': {
      backgroundColor: 'rgba(219, 103, 0, 0.05)',
    },
  },
}));

const QuestionCard = ({ item, value, onChange }) => {
  const classes = useStyles();
  const isMultiple = item.type === 'multiple';

  const handleSingle = (e) => onChange(item.id, e.target.value);
  const handleMultiple = (val, checked) => {
    const current = Array.isArray(value) ? value : [];
    const next = checked ? [...current, val] : current.filter((v) => v !== val);
    onChange(item.id, next);
  };

  return (
    <Card className={classes.card} variant="outlined">
      <CardContent>
        {item.sectionTitle && (
          <Typography className={classes.sectionTitle}>
            {item.sectionTitle}
          </Typography>
        )}
        <Typography variant="h6" className={classes.question}>
          {item.question}
        </Typography>

        {isMultiple ? (
          <FormControl component="fieldset" fullWidth>
            {item.options.map((opt) => (
              <FormControlLabel
                key={opt.value}
                control={
                  <Checkbox
                    checked={Array.isArray(value) && value.includes(opt.value)}
                    onChange={(e) => handleMultiple(opt.value, e.target.checked)}
                    color="primary"
                  />
                }
                label={opt.label}
                className={classes.option}
              />
            ))}
          </FormControl>
        ) : (
          <FormControl component="fieldset" fullWidth>
            <RadioGroup value={value || ''} onChange={handleSingle}>
              {item.options.map((opt) => (
                <FormControlLabel
                  key={opt.value}
                  value={opt.value}
                  control={<Radio color="primary" />}
                  label={opt.label}
                  className={classes.option}
                />
              ))}
            </RadioGroup>
          </FormControl>
        )}
      </CardContent>
    </Card>
  );
};

export default QuestionCard;
