import React from 'react';
import {
  Card,
  Typography,
  Box,
  Button,
  makeStyles,
} from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';

const useStyles = makeStyles((theme) => ({
  card: {
    borderRadius: 16,
    overflow: 'hidden',
    backgroundColor: '#fff',
    border: '1px solid rgba(0,0,0,0.08)',
    boxShadow: theme.shadows[1],
    height: '100%',
    minHeight: 360,
    display: 'flex',
    flexDirection: 'column',
  },
  cardDark: {
    backgroundColor: '#2d2d2d',
    border: 'none',
    color: '#fff',
  },
  header: {
    padding: theme.spacing(2, 3),
    color: '#fff',
    textAlign: 'center',
  },
  planName: {
    fontWeight: 700,
    fontSize: '1.25rem',
    color: '#fff',
    marginBottom: 4,
  },
  tagline: {
    opacity: 0.95,
    fontSize: '0.9rem',
    color: '#fff',
    marginBottom: 8,
  },
  priceWrap: {
    display: 'flex',
    flexWrap: 'wrap',
    alignItems: 'baseline',
    gap: theme.spacing(0.5),
    marginBottom: theme.spacing(1),
    justifyContent: 'center',
  },
  price: {
    fontWeight: 700,
    fontSize: '2rem',
    color: '#fff',
    lineHeight: 1.2,
  },
  priceUnit: {
    fontSize: '0.9rem',
    color: 'rgba(255,255,255,0.9)',
    lineHeight: 1,
  },
  body: {
    padding: theme.spacing(3),
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
  },
  sectionTitle: {
    fontWeight: 600,
    fontSize: '0.875rem',
    color: theme.palette.text.primary,
    marginBottom: theme.spacing(1.5),
    marginTop: theme.spacing(2),
  },
  sectionTitleFirst: {
    marginTop: 0,
  },
  featureList: {
    listStyle: 'none',
    padding: 0,
    margin: 0,
  },
  featureItem: {
    display: 'flex',
    alignItems: 'flex-start',
    gap: theme.spacing(1),
    padding: theme.spacing(0.75, 0),
    fontSize: '0.9rem',
    color: theme.palette.text.secondary,
  },
  featureItemDark: {
    color: 'rgba(255,255,255,0.9)',
  },
  benefitItem: {
    display: 'flex',
    alignItems: 'flex-start',
    gap: theme.spacing(1),
    padding: theme.spacing(0.5, 0),
    fontSize: '0.85rem',
    color: theme.palette.text.secondary,
  },
  benefitItemDark: {
    color: 'rgba(255,255,255,0.9)',
  },
  checkIcon: {
    flexShrink: 0,
    marginTop: 2,
    color: theme.palette.primary.main,
  },
  footer: {
    marginTop: 'auto',
    paddingTop: theme.spacing(2),
  },
}));

/**
 * Reusable pricing plan card.
 * @param {Object} plan - { name, tagline?, price, unit?, color, dark?, features?, benefits? }
 * @param {React.ReactNode} cta - Button/link to render as primary CTA (e.g. "Contact Us" or "Proceed to book a demo")
 * @param {React.ReactNode} [footerContent] - Optional content below CTA (e.g. match score text)
 */
const PricingCard = ({ plan, cta, footerContent }) => {
  const classes = useStyles();
  const {
    name,
    tagline = '',
    price,
    unit = '/mo',
    color,
    dark = false,
    features = [],
    benefits = [],
  } = plan;

  const priceDisplay =
    typeof price === 'number' ? `${price.toLocaleString()}` : price;

  return (
    <Card className={`${classes.card} ${dark ? classes.cardDark : ''}`} elevation={0}>
      <Box className={classes.header} style={{ backgroundColor: color }}>
        <Typography variant="h6" className={classes.planName}>
          {name}
        </Typography>
        {tagline && (
          <Typography className={classes.tagline}>{tagline}</Typography>
        )}
        <Box className={classes.priceWrap}>
          <Typography className={classes.price}>{priceDisplay}</Typography>
          <Typography className={classes.priceUnit}>{unit}</Typography>
        </Box>
      </Box>
      <Box className={classes.body}>
        {features.length > 0 && (
          <>
            <Typography className={`${classes.sectionTitle} ${classes.sectionTitleFirst}`}>
              What&apos;s included
            </Typography>
            <ul className={classes.featureList}>
              {features.map((f, i) => (
                <li
                  key={i}
                  className={`${classes.featureItem} ${dark ? classes.featureItemDark : ''}`}
                >
                  <CheckIcon className={classes.checkIcon} fontSize="small" />
                  <span>{f}</span>
                </li>
              ))}
            </ul>
          </>
        )}
        {benefits.length > 0 && (
          <>
            <Typography className={classes.sectionTitle}>Benefits</Typography>
            <ul className={classes.featureList}>
              {benefits.map((b, i) => (
                <li
                  key={i}
                  className={`${classes.benefitItem} ${dark ? classes.benefitItemDark : ''}`}
                >
                  <CheckIcon className={classes.checkIcon} fontSize="small" />
                  <span>{b}</span>
                </li>
              ))}
            </ul>
          </>
        )}
        <Box className={classes.footer}>
          {cta}
          {footerContent}
        </Box>
      </Box>
    </Card>
  );
};

export default PricingCard;
