import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Button,
  TextField,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  MenuItem,
  makeStyles,
} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { PLANS } from '../../../data/questionnaireData';
import { useContentContainerStyles } from '../../../styles/contentContainer';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2, 3, 6),
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(2, 2, 4),
    },
  },
  topBar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    marginBottom: theme.spacing(4),
    flexWrap: 'wrap',
    gap: theme.spacing(2),
  },
  backBtn: {
    textTransform: 'none',
  },
  header: {
    textAlign: 'center',
    marginBottom: theme.spacing(5),
    paddingTop: theme.spacing(2),
  },
  title: {
    fontWeight: 700,
    fontSize: '2rem',
    color: '#1a1a1a',
    marginBottom: theme.spacing(1),
  },
  subtitle: {
    color: '#666',
    fontSize: '1.05rem',
  },
  content: {
    maxWidth: 560,
    margin: '0 auto',
  },
  formSection: {
    marginTop: theme.spacing(2),
  },
  contactInfo: {
    display: 'flex',
    flexDirection: 'column',
    gap: theme.spacing(2),
    marginBottom: theme.spacing(4),
  },
  infoRow: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(2),
    color: '#555',
    '& svg': {
      color: '#DB6700',
      fontSize: 22,
    },
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: theme.spacing(3),
  },
  submitBtn: {
    alignSelf: 'flex-start',
    padding: theme.spacing(1.5, 3),
    textTransform: 'none',
  },
  intentGroup: {
    marginBottom: theme.spacing(3),
  },
  intentLabel: {
    fontWeight: 600,
    marginBottom: theme.spacing(1),
  },
}));

const INTENT_DEMO = 'demo';
const INTENT_INQUIRY = 'inquiry';
const planList = Object.values(PLANS);

const ContactUs = () => {
  const classes = useStyles();
  const contentClasses = useContentContainerStyles();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const planParam = searchParams.get('plan');

  const [intent, setIntent] = useState(INTENT_DEMO);
  const [plan, setPlan] = useState(planParam || '');

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    if (planParam && planList.some((p) => p.id === planParam)) {
      setPlan(planParam);
    }
  }, [planParam]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (intent === INTENT_DEMO) {
      window.alert('Thank you! We\'ll reach out to schedule your demo. (Demo)');
    } else {
      window.alert('Thank you! We will get back to you soon. (Demo)');
    }
  };

  return (
    <Container maxWidth="md" className={`${classes.root} ${contentClasses.root}`}>
      <Box className={classes.topBar}>
        <Button
          component="button"
          onClick={() => navigate(-1)}
          startIcon={<ArrowBackIcon />}
          className={classes.backBtn}
          color="primary"
        >
          Back
        </Button>
      </Box>

      <Box className={classes.header} component="header">
        <Typography id="contact-heading" variant="h1" component="h1" className={classes.title}>
          Contact Us
        </Typography>
        <Typography variant="body1" className={classes.subtitle}>
          Book a demo or reach outwe&apos;re here to help.
        </Typography>
      </Box>

      <Box className={classes.content} component="section" aria-labelledby="contact-heading">

        <form onSubmit={handleSubmit} className={`${classes.form} ${classes.formSection}`}>
          <FormControl component="fieldset" className={classes.intentGroup}>
            <FormLabel className={classes.intentLabel}>What would you like to do?</FormLabel>
            <RadioGroup
              row
              value={intent}
              onChange={(e) => setIntent(e.target.value)}
            >
              <FormControlLabel value={INTENT_DEMO} control={<Radio color="primary" />} label="Book a demo" />
              <FormControlLabel value={INTENT_INQUIRY} control={<Radio color="primary" />} label="General inquiry" />
            </RadioGroup>
          </FormControl>

          {intent === INTENT_DEMO && (
            <TextField
              select
              label="Interested plan"
              variant="outlined"
              fullWidth
              value={plan}
              onChange={(e) => setPlan(e.target.value)}
            >
              <MenuItem value="">Select a plan</MenuItem>
              {planList.map((p) => (
                <MenuItem key={p.id} value={p.id}>{p.name}</MenuItem>
              ))}
            </TextField>
          )}

          <TextField label="Name" variant="outlined" fullWidth required />
          <TextField label="Email" type="email" variant="outlined" fullWidth required />
          <TextField label="Company (optional)" variant="outlined" fullWidth />
          <TextField
            label="Message"
            variant="outlined"
            fullWidth
            multiline
            rows={4}
            placeholder={intent === INTENT_DEMO ? 'Tell us about your payroll needs or preferred demo date...' : 'How can we help?'}
          />
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.submitBtn}
          >
            {intent === INTENT_DEMO ? 'Book a demo' : 'Send inquiry'}
          </Button>
        </form>
      </Box>
    </Container>
  );
};

export default ContactUs;
