import { questionnaireData, PLANS } from '../data/questionnaireData';

export const computeScore = (answers) => {
  let total = 0;

  questionnaireData.forEach((q) => {
    const answer = answers[q.id];
    if (answer == null) return;

    if (q.type === 'single') {
      const opt = q.options.find((o) => o.value === answer);
      if (opt) total += opt.score;
    } else if (q.type === 'multiple' && Array.isArray(answer)) {
      answer.forEach((val) => {
        const opt = q.options.find((o) => o.value === val);
        if (opt) total += opt.score;
      });
      if (q.complexityBonus && q.complexTypes) {
        const complexSelected = answer.filter((v) => q.complexTypes.includes(v));
        if (complexSelected.length >= (q.bonusThreshold || 3)) {
          total += q.bonusPoints || 2;
        }
      }
    }
  });

  return total;
};

/** Max possible score from questionnaire (for display) */
export const getMaxScore = () => {
  let max = 0;
  questionnaireData.forEach((q) => {
    if (q.type === 'single') {
      const highest = Math.max(...q.options.map((o) => o.score || 0));
      max += highest;
    } else if (q.type === 'multiple' && q.options) {
      const scores = q.options.map((o) => o.score || 0);
      max += scores.reduce((a, b) => a + b, 0);
      if (q.complexityBonus && q.bonusPoints) max += q.bonusPoints;
    }
  });
  return max;
};

/**
 * Get recommended plan from score
 * 1-10: Starter, 11-20: Professional, 21+: Enterprise
 * Scores above 30 still map to Enterprise (complex needs)
 */
export const getRecommendedPlan = (score) => {
  const plans = Object.values(PLANS);
  const plan = plans.find((p) => score >= p.scoreRange[0] && score <= p.scoreRange[1]);
  if (plan) return plan;
  // Score exceeds Enterprise range (21-30)  still Enterprise
  return plans[plans.length - 1] || plans[0];
};
