import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Box,
  makeStyles,
} from '@material-ui/core';
import DescriptionIcon from '@material-ui/icons/Description';

const useStyles = makeStyles((theme) => ({
  card: {
    borderRadius: 12,
    boxShadow: theme.shadows[2],
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardContent: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(1),
    marginBottom: theme.spacing(2),
  },
  row: {
    padding: theme.spacing(1.25, 0),
  },
  question: {
    fontWeight: 500,
    fontSize: '0.85rem',
    color: theme.palette.text.primary,
    marginBottom: theme.spacing(0.5),
  },
  answer: {
    color: theme.palette.text.secondary,
    fontSize: '0.875rem',
    wordBreak: 'break-word',
    overflowWrap: 'break-word',
  },
}));

const getAnswerLabel = (item, value) => {
  if (value == null) return '';
  if (Array.isArray(value)) {
    return item.options
      .filter((o) => value.includes(o.value))
      .map((o) => o.label)
      .join(', ') || '';
  }
  const opt = item.options.find((o) => o.value === value);
  return opt ? opt.label : value;
};

const AnswerSummary = ({ answers, questionnaireData }) => {
  const classes = useStyles();

  return (
    <Card className={classes.card} variant="outlined">
      <CardContent className={classes.cardContent}>
        <Box className={classes.header}>
          <DescriptionIcon color="primary" fontSize="small" />
          <Typography variant="subtitle1" style={{ fontWeight: 600 }}>
            Your answers summary
          </Typography>
        </Box>
        {questionnaireData.map((item) => (
          <Box key={item.id} className={classes.row}>
            <Typography className={classes.question}>{item.question}</Typography>
            <Typography className={classes.answer} component="div">
              {getAnswerLabel(item, answers[item.id])}
            </Typography>
          </Box>
        ))}
      </CardContent>
    </Card>
  );
};

export default AnswerSummary;
