import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Link,
  Grid,
  makeStyles,
} from '@material-ui/core';
import { useContentContainerStyles } from '../../styles/contentContainer';

const useStyles = makeStyles((theme) => ({
  footer: {
    backgroundColor: '#1a1a1a',
    color: '#fff',
    padding: theme.spacing(6, 2),
  },
  logo: {
    fontWeight: 700,
    fontSize: '1.25rem',
    color: '#fff',
    marginBottom: theme.spacing(2),
  },
  copyright: {
    color: 'rgba(255,255,255,0.7)',
    fontSize: '0.9rem',
    marginBottom: theme.spacing(3),
  },
  sectionTitle: {
    fontWeight: 600,
    fontSize: '0.9rem',
    marginBottom: theme.spacing(1.5),
    color: '#fff',
  },
  footerLink: {
    display: 'block',
    color: 'rgba(255,255,255,0.8)',
    fontSize: '0.875rem',
    marginBottom: theme.spacing(0.5),
    textDecoration: 'none',
    '&:hover': {
      color: '#DB6700',
    },
  },
}));

const quickLinks = [
  { label: 'Home', to: '/' },
  { label: 'Plan Recommendation', to: '/plan-recommendation' },
  { label: 'Contact Us', to: '/contact-us' },
];

const products = [
  { label: 'UlapPayroll' },
  { label: 'UlapTax' },
  { label: 'Accounting and Beyond' },
];

const Footer = () => {
  const classes = useStyles();
  const contentClasses = useContentContainerStyles();

  return (
    <Box component="footer" className={classes.footer}>
      <Container maxWidth="lg" className={contentClasses.root}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={4}>
            <Typography className={classes.logo}>UlapBiz</Typography>
            
            <Typography className={classes.copyright}>
              Copyright  {new Date().getFullYear()} UlapBiz. All rights reserved.
            </Typography>
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Typography className={classes.sectionTitle}>Quick links</Typography>
            {quickLinks.map((item, i) => (
              <Link
                key={i}
                component={RouterLink}
                to={item.to}
                className={classes.footerLink}
              >
                {item.label}
              </Link>
            ))}
          </Grid>
          <Grid item xs={12} sm={6} md={4}>
            <Typography className={classes.sectionTitle}>Products</Typography>
            {products.map((item, i) => (
              <Typography
                key={i}
                className={classes.footerLink}
                component={Link}
                href="#"
                style={{ cursor: 'pointer' }}
              >
                {item.label}
              </Typography>
            ))}
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Footer;
